:root {
  --content-width: 640px;
  --text-color: #444;
  --background-color: #fffdfc;
}

html, body {
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
  font-family: sans-serif;
}

.screen {
  width: 100vw;
  height: 100vh;
}

.content {
  width: var(--content-width);
  max-width: 100%;
}

.content.padding {
  padding: 8px;
}

.overlay {
  background-color: #0000001f;
  border-radius: 3px;
